@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');

body {
  margin: 0;
  font-family: 'Pretendard';
}

body * {
  font-family: 'Pretendard';
}

.examRegister {
  display: flex;
  flex-direction: row;
  
  width: 100vw;
  height: 100vh;
  
  background-color: rgba(255,255,255,1);


  .examReigsterMargin {
    display: flex;
    flex-direction: column;

    .examRegisterTitle {
      display: flex;
      
      font-size: 14pt;
      font-weight: 700;
      color: rgba(10,10,10,1);

      margin-top: 64px;
      margin-left: 72px;
    }

    .examRegisterFormMain {
      display: flex;
      flex-direction: column;

      margin-top: 16px;
      margin-left: 72px;

      .examRegisterFormFirst {
        display: flex;
        flex-direction: column;

        .textMain {
          display: flex;
          background-color: rgba(246, 246, 246, 1);
          width: 600px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700;
        }

        .textMain:hover {
          border: 1px solid blue;
        }

        .textMain::placeholder {
          display: flex;
        }
      }

      .examRegisterFormSecond {
        display: flex;
        flex-direction: row;

        margin-top: 16px;

        .schoolMain {
          display: flex;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700;
        }

        .schoolMain:hover {
          border: 1px solid blue;
        }

        .schoolMain::placeholder {
          display: flex;
        }

        select {
          margin-left: 28px;
          width: 292px;
          height: 58px;
          border: 0;
          border-radius: 5px;
          outline: none;
          padding-left: 11px;
          background-color: rgba(246,246,246,1);
        }
        
        select option {
          display: flex;
          font-weight: 700;
          font-size: 10pt;
          color: rgba(10,10,10,1);
        }
      }

      .examRegisterFormThird {
        display: flex;
        flex-direction: row;

        margin-top: 16px;

        .averageMain {
          display: flex;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .averageMain:hover {
          border: 1px solid blue;
        }

        .deviationMain {
          display: flex;
          margin-left: 28px;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .deviationMain:hover {
          border: 1px solid blue;
        }
      }

      .examRegisterFormFourth {
        display: flex;
        flex-direction: column;

        margin-top: 16px;

        .participantsMain {
          display: flex;
          background-color: rgba(246, 246, 246, 1);
          width: 600px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .participantsMain:hover {
          border: 1px solid blue;
        }
      }

      .examRegisterFormFifth {
        display: flex;
        flex-direction: row;

        margin-top: 16px;

        .testNameMain {
          display: flex;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .testNameMain:hover {
          border: 1px solid blue;
        }

        .subjectMain {
          display: flex;
          margin-left: 28px;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .subjectMain:hover {
          border: 1px solid blue;
        }
      }

      .examRegisterFormSixth {
        display: flex;
        flex-direction: row;

        margin-top: 16px;

        .firstTestMain {
          display: flex;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .firstTestMain:hover {
          border: 1px solid blue;
        }

        .secondTestMain {
          display: flex;
          margin-left: 28px;
          background-color: rgba(246, 246, 246, 1);
          width: 280px;
          height: 56px;
          border-radius: 8px;
          border: none;
          padding-left: 10px;

          font-size: 10pt;
          font-weight: 700; 
        }

        .secondTestMain:hover {
          border: 1px solid blue;
        }
      }

      .buttonMain {
        display: flex;
        flex-direction: row;

        margin-top: 64px;

        .userRegister {
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 8px;
          
          width: 160px;
          height: 56px;
          background-color: rgba(76, 168, 115, 1);
          cursor: pointer;

          .userRegisterText {
            display: flex;
            font-size: 12pt;
            font-weight: 600;
            color: rgba(255, 255, 255, 1);
          }
        }

        .userRegister:hover {
          display: flex;
  
          transition: 0.3s ease;
          background-color: #1957c2;
        }

        .resetUser {
          display: flex;
          margin-left: 32px;
          align-items: center;
          justify-content: center;
          border-radius: 8px;
          
          width: 160px;
          height: 56px;
          background-color: rgba(125, 125, 125, 1);
          cursor: pointer;

          .userRegisterText {
            display: flex;
            font-size: 12pt;
            font-weight: 600;
            color: rgba(255, 255, 255, 1);
          }
        }

        .resetUser:hover {
          display: flex;
  
          transition: 0.3s ease;
          background-color: #1957c2;
        }
      }
    }
  }
}