@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css');

body {
  margin: 0;
  font-family: 'Pretendard';
}

body * {
  font-family: 'Pretendard';
}

.main {
  display: flex;
  flex-direction: column;

  width: 100vw;
  height: 100vh;

  background-color: #D9D9D9;

  justify-content: center;
  align-items: center;
  
  img {
    display: flex;

    width: 212px;
    height: 250px;
    margin-bottom: 30px;
  }

  .mainScreenList {
    display: flex;
    flex-direction: row;
    
    .mainScreenListStudentRegister {
        display: flex;
        flex-direction: column;

        .registerIcon {
            display: flex;
            font-size: 30pt;
            cursor: pointer;
        }

        .studentRegisterText {
            display: flex;
            font-size: 12pt;
            font-weight: 700;

            margin-top: 5px;
            cursor: pointer;
        }
    }

    .mainScreenListExamRegister {
        display: flex;
        flex-direction: column;

        margin-left: 30px;

        .registerIcon {
            display: flex;
            font-size: 30pt;
            cursor: pointer;
        }

        .examRegisterText {
            display: flex;
            font-size: 12pt;
            font-weight: 700;

            margin-top: 5px;
            cursor: pointer;
        }
    }

    .mainScreenListExamList {
        display: flex;
        flex-direction: column;

        margin-left: 30px;

        .registerIcon {
            display: flex;
            font-size: 30pt;
            cursor: pointer;
        }

        .examListText {
            display: flex;
            font-size: 12pt;
            font-weight: 700;

            margin-top: 5px;
            cursor: pointer;
        }
    }

    .mainScreenListStudentList {
        display: flex;
        flex-direction: column;

        margin-left: 30px;

        .registerIcon {
            display: flex;
            font-size: 30pt;
            cursor: pointer;
        }

        .studentListText {
            display: flex;
            font-size: 12pt;
            font-weight: 700;

            margin-top: 5px;
            cursor: pointer;
        }
     }

     .mainScreenListStudentList:hover {
        transition: 0.3s ease;
        color: rgba(10,10,10,0.5);
     }

     .mainScreenListExamList:hover {
        transition: 0.3s ease;
        color: rgba(10,10,10,0.5);
     }

     .mainScreenListStudentRegister:hover {
        transition: 0.3s ease;
        color: rgba(10,10,10,0.5);
     }

     .mainScreenListExamRegister:hover {
        transition: 0.3s ease;
        color: rgba(10,10,10,0.5);
     }
   }
}