/* src/App.css */
.sidebar {
  display: flex;
  flex-direction: column;

  height: 100vh;
  width: 280px;

  background-color: rgba(255,255,255,1);
  box-shadow: 15px 0px 20px -2px rgba(125,125,125,0.25);
}

.sidebar .sideBarMain {
  display: flex;
  flex-direction: column;

  margin-left: 32px;
  margin-top: 64px;
}

.sidebar .sideBarMain .sideBarMainImage {
  display: flex;

  width: 80px;
  height: 80px;
  border-radius: 50px;

  background-color: #D9D9D9;
}

.sidebar .sideBarMain .sideBarMainText {
  display: flex;

  margin-top: 23px;
  color: rgba(10,10,10,1);
  font-size: 20pt;
  font-weight: 700;
}

.sidebar .sideBarMain .sideBarSubMainText {
  display: flex;

  margin-top: 10px;
  color: rgba(10,10,10,1);
  font-size: 12pt;
  font-weight: 500;
}

.sidebar .sideBarDivLine {
  display: flex;
  margin-top: 92px;

  width: 280px;
  height: 2px;

  background-color: rgba(246,246,246,1);
}

.sidebar .sideBarMenu {
  display: flex;
  flex-direction: column;

  margin-left: 32px;
  margin-top: 25px;
}

.sidebar .sideBarMenu .sideBarMenuMainText {
  display: flex;
  align-items: center;

  font-size: 14pt;
  color: rgba(10,10,10,1);
  font-weight: 700;
}

.sidebar .sideBarMenu .sideBarMenuMainText .sideBarMenuIcon {
  display: flex;
  margin-right: 10px;
}

.sidebar .sideBarMenuMain {
  display: flex;
  flex-direction: column;

  margin-top: 24px;
  margin-left: 32px;
}

.selected {
  margin-bottom: 12px;
}

.link {
  text-decoration: none;
  color: rgba(114,120,127,1);
  font-size: 12pt;
  font-weight: 700;
}

.selected .link {
  color: rgba(69,76,83,1); /* Ensure selected link text color changes */
}

.simple {
  background-color: transparent;
  margin-bottom: 12px;
}

.simpleMargin {
  margin-right: 12px;
}

.simpleMarginTest {
  margin-left: 4px;
  margin-right: 16px;
}