.studentListScreen {
  display: flex;
  flex-direction: row;

  width: 100vw;
  height: 100vh;

  background-color: rgba(255,255,255,1);
  user-select: none;

  .studentListScreenCenter {
    display: flex;
    flex-direction: column;
    
    overflow-y: auto;
    width: 100vw;

    .studentListScreenCenterMainSearch {
      display: flex;
      flex-direction: row;

      margin-top: 64px;
      margin-left: 72px;

      input {
        display: flex;
        font-family: FontAwesome, 'Pretendard';

        color: rgba(10,10,10,0.5);
        padding-left: 20px;
        font-weight: 700;
        font-size: 14pt;
        width: 600px;
        height: 56px;
        background-color: rgba(246,246,246,1);
        border-radius: 40px;
        border: none;
      }

      input:hover {
        border: 1px solid blue;
      }

      input::placeholder {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 14pt;
      }
    }

    .studentListScreenCenterMainTable {
      display: flex;
      flex-direction: column;

      padding: 72px;

      .studentListDivLine {
        display: flex;
  
        width: 100%;
        height: 1px;
        background-color: rgba(125, 125, 125, 1);
      }

      table {
        border-collapse: collapse;
        width: 100%;
      }

      th, td {
        padding: 15px;
        text-align: left;
      }

      th {
        font-size: 12pt;
        font-weight: 700;
        padding: 15px;
        border-bottom: 1px solid rgba(125, 125, 125, 1);
      }

      td {
        font-size: 12pt;
        font-weight: 500;
      }

      tbody tr:nth-child(odd) {
        background-color: rgba(246,246,246,1);
      }

      .school {
        width: 423px;
      }

      .grade {
        width: 120px;
      }

      .name {
        width: 137px;
      }

      .phone {
        width: auto; /* 전화번호는 나머지 너비로 */
      }
    }
  }
}