.registerMain {
    display: flex;
    flex-direction: row;
  
    width: 100vw;
    height: 100vh;
    
    background-color: rgba(255,255,255,1);
    user-select: none;

    .registerMainCenter {
      display: flex;
      flex-direction: column;

      .registerMainCenterSchoolInfo {
        display: flex;
        flex-direction: column;

        margin-top: 64px;
        margin-left: 72px;

        .registerMainCenterSchoolInfoText {
          display: flex;

          font-weight: 700;
          font-size: 14pt;
          color: rgba(10,10,10,1);
        }

        input::placeholder {
          display: flex;
          font-weight: 700;
          font-size: 10pt;
          color: rgba(10,10,10,0.5);
        }
        
        input:hover {
          border: 1px solid blue;
        }
        
        input:focus {
          border: 1px solid blue;
        }
        
        select {
          width: 612px;
          height: 58px;
          border: 0;
          border-radius: 5px;
          outline: none;
          padding-left: 11px;
          margin-top: 20px;
          background-color: rgba(246,246,246,1);
        }
        
        select option {
          display: flex;
          font-weight: 700;
          font-size: 10pt;
          color: rgba(10,10,10,1);
        }
      }
    }

    .registerMainCenterStudentInfo {
      display: flex;
      flex-direction: column;

      margin-top: 48px;
      
      .registerMainCenterStudentInfoText {
        display: flex;

        font-weight: 700;
        font-size: 14pt;
        color: rgba(10,10,10,1);
      }
    }

    .textMain {
      display: flex;
      font-weight: 700;
      font-size: 10pt;
      color: rgba(10,10,10,1);
    }

    .textMainSchool {
      display: flex;
      font-weight: 700;
      font-size: 10pt;
      color: rgba(10,10,10,1);
    }

    .phoneNumber {
      display: flex;
      font-weight: 700;
      font-size: 10pt;
      color: rgba(10,10,10,1);
    }

    .buttonMain {
      display: flex;
      flex-direction: row;
      margin-top: 64px;
      margin-left: 72px;

      .resetUser {
        display: flex;
        flex-direction: column;
        background-color: rgba(125,125,125,1);
        width: 160px;
        height: 56px;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        margin-left: 30px;
        cursor: pointer;

        .userRegisterText {
          display: flex;
          font-size: 12pt;
          font-weight: 600;
          color: rgba(255, 255, 255, 1);
        }
      }

      .resetUser:hover {
        display: flex;

        transition: 0.3s ease;
        background-color: #1957c2;
      }

      .userRegister:hover {
        display: flex;

        transition: 0.3s ease;
        background-color: #1957c2;
      }

      .userRegister {
        display: flex;
        flex-direction: column;
        width: 160px;
        border-radius: 8px;
        align-items: center;
        justify-content: center;
        background-color: rgba(76,168,115,1);
        cursor: pointer;

        .userRegisterText {
          display: flex;
          font-size: 12pt;
          font-weight: 600;
          color: rgba(255, 255, 255, 1);
        }
      }
    }
}

.textMainSchool {
  width: 600px;
  margin-top: 16px;
  height: 56px;
  border: 0;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
  background-color: rgba(246,246,246,1);
}

.registerMainInputDivStudent {
  display: flex;
  flex-direction: row;
}

.textMainStudent {
  width: 280px;
  margin-top: 16px;
  height: 56px;
  border: 0;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
  background-color: rgba(246,246,246,1);
}

.textMainStudentMargin {
  width: 280px;
  margin-top: 16px;
  height: 56px;
  border: 0;
  border-radius: 5px;
  outline: none;
  padding-left: 10px;
  margin-left: 40px;
  background-color: rgba(246,246,246,1);
}
