@media print {
  @page {
    size: auto;
    margin: 0; // 페이지 여백 제거
  }

  * {
    -webkit-print-color-adjust: exact; /* Chrome, Edge */
    print-color-adjust: exact; /* Firefox */
    color-adjust: exact; /* Older syntax, 일부 브라우저에서 지원 */
  }

  body {
    display: flex;
    justify-content: center; // 가로 가운데 정렬
    align-items: center; // 세로 가운데 정렬
    min-height: 100vh; // 화면 높이 100%
    margin: 0;
    -webkit-print-color-adjust: exact !important;
    print-color-adjust: exact !important;
  }

  .examReportMainScreen {
    display: flex;
    flex-direction: column;
    justify-content: center; // 화면 중앙 배치
    align-items: center;
    margin: 0 auto; // 중앙 정렬
    width: 100%;
    max-width: 800px; // 최대 너비 설정
    page-break-after: avoid;
  }

  .no-print {
    display: none; // 프린트 시 숨길 요소
  }
}


.examReportMainScreen {
  display: flex;
  flex-direction: row;
  height: 100vh;
  width: 100vw;

  margin-right: 100px;
  margin-left: 100px;

  .examReportHeader {
    display: flex;
    flex-direction: row;
    
    margin-top: 75px;
    padding: 40px;
    background-color: #265c5b;

    .examReportHeaderLeft {
      display: flex;
      flex-direction: column;

      .examReportHeaderTitleMain {
        display: flex;
        color: rgba(250,250,250,1);
        font-size: 24px;
        font-weight: 700;
      }
  
      .examReportHeaderTitle {
        display: flex;
        color: rgba(250,250,250,0.8);
        font-size: 22px;
        font-weight: 500;
        margin-top: 10px;
      }
    }

    .examReportHeaderRight {
      display: flex;
      flex-direction: column;

      margin-left: auto;
      margin-top: auto;

      .examReportHeaderStudentName {
        display: flex;
        
        color: rgba(250,250,250,1);
        font-size: 22px;
        font-weight: 700;
      }
    }
  }

  .examReportRightIntro {
    display: flex;
    flex-direction: column;
    margin-top: 20px;

    .examReportRightTop {
      display: flex;
      flex-direction: row;

      align-items: center;

      .examReportRightTitle {
        display: flex;

        color: rgba(255,165,0,1);
        font-size: 12pt;
        font-weight: 700;
      }

      .bar {
        display: flex;
        width: 1px;
        height: 50px;
        margin-left: 42px;
        background-color: black;
      }

      .examReportRightInfo {
        display: flex;
        margin-left: 10px;

        color: rgba(10,10,10,1);
        font-size: 10pt;
        font-weight: 700;
      }
    }

    .examReportRightBottom {
      display: flex;
      flex-direction: row;

      margin-top: 20px;
      align-items: center;

      .examReportRightBottomTitle {
        display: flex;

        color: rgba(255,165,0,1);
        font-size: 12pt;
        font-weight: 700;
      }

      .bar {
        display: flex;
        width: 1px;
        height: 30px;
        margin-left: 10px;
        background-color: black;
      }

      .examReportRightBottomInfo {
        display: flex;
        margin-left: 10px;

        color: rgba(10,10,10,1);
        font-size: 10pt;
        font-weight: 700;
      }
    }

    .examReportRightBottomTable {
      display: flex;
      flex-direction: column;

      margin-left: 140px;

      .grade-table {
        width: 100%; /* 테이블 너비 설정 */
        border-collapse: collapse;
        text-align: center;
        font-family: Arial, sans-serif;
        margin: 20px 0;
      }
      
      .grade-table th,
      .grade-table td {
        border: 1px solid #ddd;
        padding: 2px; /* padding을 더 줄이기 */
        height: 25px; /* 셀 높이 줄이기 */
      }
      
      .grade-table th {
        background-color: #f4f4f4;
        font-weight: bold;
        color: #333;
      }
      
      .grade-table td {
        font-size: 14px;
        color: #555;
      }
      
      .grade-table tr:nth-child(even) {
        background-color: #f9f9f9;
      }
      
      .grade-table tr:hover {
        background-color: #f1f1f1;
      }
      
      .grade-table th:first-child,
      .grade-table th:nth-child(3),
      .grade-table td:first-child,
      .grade-table td:nth-child(3) {
        width: 15%;
      }
      
      .grade-table th:nth-child(2),
      .grade-table th:nth-child(4),
      .grade-table td:nth-child(2),
      .grade-table td:nth-child(4) {
        width: 20%;
      }
      
      .grade-table th:last-child,
      .grade-table td:last-child {
        width: 30%;
      }      
    }

    .examReportRightBottomTableIntro {
      display: flex;
      flex-direction: row;

      align-items: center;

      .examReportRightBottomTableTitle {
        display: flex;

        color: rgba(255,165,0,1);
        font-size: 12pt;
        font-weight: 700;
      }

      .bar {
        display: flex;
        width: 1px;
        height: 30px;
        margin-left: 35px;
        background-color: black;
      }

      .examReportRightBottomTableInfo {
        display: flex;
        margin-left: 10px;

        color: rgba(10,10,10,1);
        font-size: 10pt;
        font-weight: 700;
      }
    }
  }

  .examReportMainScoreInfo {
    display: flex;
    flex-direction: row;

    margin-top: 20px;
    background-color: rgba(255,160,122,1);
    padding: 40px;

    .examReportMainScoreInfoLeft {
      display: flex;
      flex-direction: column;

      .examReportMainScoreInfoTitle {
        display: flex;
        
        color: rgba(250,250,250,1);
        font-size: 14pt;
        font-weight: 700;
      }

      .examReportMainScoreInfoSubTitle {
        display: flex;
        margin-top: 5px;

        color: rgba(250,250,250,0.8);
        font-size: 14pt;
        font-weight: 700;
      }
    }

    .examReportMainScoreInfoMiddle {
      display: flex;
      flex-direction: row;

      align-items: center;
      justify-content: center;
      margin-left: 50px;

      .examReportMainScoreMiddleMap {
        display: flex;
        flex-direction: row;

        align-items: center;
        justify-content: center;

        .examReportStudentGrade {
          display: flex;
  
          color: rgba(255,215,0,1);
          font-size: 34pt;
          font-weight: 700;
        }

        .examReportStudentGradeSub {
          display: flex;
          margin-top: 15px;

          color: rgba(255,215,0,1);
          font-size: 12pt;
          font-weight: 700;
        }
      }
    }

    .examReportMainScoreInfoRight {
      display: flex;
      flex-direction: column;

      margin-left: auto;

      .examReportMainScoreInfoRightStudentScore {
        display: flex;
        flex-direction: row;

        align-items: center;
        margin-bottom: 10px;

        .examReportMyScoreTitle {
          display: flex;
          color: rgba(250,250,250,1);
          font-size: 18pt;
          font-weight: 700;
        }

        .examReportMyScoreSubTitle {
          display: flex;
          color: rgba(250,250,250,1);
          font-size: 18pt;
          margin-left: 5px;
          font-weight: 700;
        }
      }

      .examReportMainScoreInfoRightMainDiv {
        display: flex;
        flex-direction: row;

        .examReportMainScoreInfoRightStudentZValue {
          display: flex;
          flex-direction: column;

          align-items: center;
          
          .examReportMyZValueTitle {
            display: flex;
            color: rgba(250,250,250,1);
            font-size: 10pt;
            font-weight: 700;
          }

          .examReportMyZValueSubTitle {
            display: flex;
            color: rgba(250,250,250,1);
            font-size: 10pt;
            font-weight: 700;
          }
        }
  
        .examReportMainScoreInfoRightStudentDensity {
          display: flex;
          flex-direction: column;

          margin-left: auto;
          align-items: center;

          .examReportMyDensityTitle {
            display: flex;
            color: rgba(250,250,250,1);
            font-size: 10pt;
            font-weight: 700;
          }

          .examReportMyDensitySubTitle {
            display: flex;
            color: rgba(250,250,250,1);
            font-size: 10pt;
            font-weight: 700;
          }
        }
      }
    }
  }
  .examReportMainScoreInfoDetail {
    display: flex;
    flex-direction: row;
    margin-top: 5px;
    margin-bottom: 20px;
    background-color: rgba(10,10,10,0.2);

    padding: 40px;

    .rank {
      display: flex;
      flex-direction: column;
      align-items: center;

      .rankTitle {
        display: flex;

        color: rgba(250,250,250,0.8);
        font-size: 14pt;
        font-weight: 700;
      }

      .rankSubTitle {
        display: flex;
        margin-top: 10px;
        color: rgba(250,250,250,1);
        font-size: 18pt;
        font-weight: 700;
      }
    }

    .percentile {
      display: flex;
      flex-direction: column;

      align-items: center;
      margin-left: auto;

      .percentileTitle {
        display: flex;

        color: rgba(250,250,250,0.8);
        font-size: 14pt;
        font-weight: 700;
      }

      .percentileSubTitle {
        display: flex;
        margin-top: 10px;
        color: rgba(250,250,250,1);
        font-size: 18pt;
        font-weight: 700;
      }
    }

    .totalpeople {
      display: flex;
      flex-direction: column;

      align-items: center;
      margin-left: auto;

      .totalPeopleTitle {
        display: flex;

        color: rgba(250,250,250,0.8);
        font-size: 14pt;
        font-weight: 700;
      }

      .totalPeopleSubTitle {
        display: flex;

        margin-top: 10px;
        color: rgba(250,250,250,1);
        font-size: 18pt;
        font-weight: 700;
      }
    }

    .schoolaverage {
      display: flex;
      flex-direction: column;

      align-items: center;
      margin-left: auto;

      .schoolAverageTitle {
        display: flex;

        color: rgba(250,250,250,0.8);
        font-size: 14pt;
        font-weight: 700;
      }

      .schoolAverageSubTitle {
        display: flex;

        margin-top: 10px;
        color: rgba(250,250,250,1);
        font-size: 18pt;
        font-weight: 700;
      }
    }

    .schoolstandarddeviation {
      display: flex;
      flex-direction: column;

      align-items: center;
      margin-left: auto;

      .deviationTitle {
        display: flex;

        color: rgba(250,250,250,0.8);
        font-size: 14pt;
        font-weight: 700;
      }

      .deviationSubTitle {
        display: flex;

        margin-top: 10px;
        color: rgba(250,250,250,1);
        font-size: 18pt;
        font-weight: 700;
      }
    }
    .chart-row-container {
      display: flex;
      width: 100%;
      gap: 20px; /* 두 차트 사이의 간격 */
    }
    
    .chart-container {
      flex: 1; /* 각 차트의 너비를 균등하게 설정 */
      max-width: 100%; /* 부모 요소의 100% 너비 */
    }
    
  }
}